* {
    margin: 0 !important;
    padding: 0 !important;
    box-sizing: border-box;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  }
  body{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:#D1D5DB;
    height: 120vh;
    overflow-x: hidden;
  }
  .todo{
    width: 440px;
    height: 700px;
    border-radius: 3px;
    background-color: rgba(229, 231, 235, 0.863);
    box-shadow: 0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);;
    display: flex;
    align-content: flex-start;
  }
  .header{
    width: 100%;
    height: 130px;
    position: relative;
    top: 0;left: 0;
    color: white;
  }
  .img{
    height: 100%;
    position: absolute;
    top: 0;left: 0;
    z-index: 1;
  }
  .header>h1{
    width: 100%;
    height: 65px;
    position: absolute;
    z-index: 2;
    font-size: 1.5rem;
    line-height: 2rem;
    display: flex;
    align-items: flex-end;
    padding-left: 20px !important;
  }
  .header>p{
    width: 100%;
    height: 65px;
    position: absolute;
    top: 65px;
    z-index: 2;
    font-size: 1.125rem;
    line-height: 1.75rem;
    padding-top: 5px !important;
    padding-left: 20px !important;
  }
  .form{
    border-bottom: 1px solid #939697;
    width: 90%;
    height: 45px;
    margin-left: 5% !important;
    margin-top: 20px !important;
    display: flex;
    align-items: center;
  }
  .form>input{
    width: 90%;
    height: 40px;
    background-color: #E5E7EB;
    border: none;
    outline: none;
  }
  .form>button{
    width: 10%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color:  #E5E7EB;
  }
  .form>button:hover i{
    color: #EC4899;
  }
  .form>button>i{
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    color: #89898a;
  }
  .ul{
    width: 90%;
    min-height: 300px;
    margin-left: 5% !important;
    margin-top: 50px !important;
    list-style-type: none;
  }
  .li{
    width: 100%;
    height: 45px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid  #89898a8f;
  }
  .li>span{
    display: flex;
    align-items: center;
    height: 100%;
    width: 90%;
    margin-left: 10px !important;
  }
  .li>button{
    width: 10%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color:  #E5E7EB;
  }
  .li>button>i{
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    color: #89898a;
  }
  .li>button:hover i{
    color: #EC4899;    
  }
  .top{
    width: 90%;
    height: 80%;
    margin-left:5% !important ;
    display: flex;
    align-content: flex-start;
  }
  .top>p{
   position: absolute;
   top: 65%;left: 45%;
  
  }